<template>
	<div class="todo-item-box">
		<span
			class="icon material-icons"
			:class="prop.checked ? 'color-success' : 'color-danger'"
		>
			{{prop.checked ? 'done' : 'close'}}
		</span>
		<div class="text-wrap">
			<p>{{prop.text}}</p>
		</div>
		<div class="btn-wrap">
			<button class="btn btn-outline-primary" @click="changeItem" title="Изменить задачу">
				<i class="material-icons">edit</i>
			</button>
		</div>
	</div>
</template>

<script>
	import {modalConfig} from "../plugins/modal";
	export default {
		name: "TodoItem",
		props: ['prop'],
		components: {
		},
		methods: {
			changeItem() {
				this.$router.push({
					path: '/form',
					query: {id: this.prop.id}
				})
			}
		}
	}
</script>

<style lang="scss">
	.todo-item-box {
		display: flex;
		align-items: center;
		justify-content: space-between;
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
		& .text-wrap {
			margin-left: 1rem;
			margin-right: 1rem;
			height: 100%;
			display: flex;
			align-items: center;
			flex-grow: 1;
		}
	}
</style>