<template>
	<div class="modal-container" :class="{'active': visible}">
		<div class="modal">
			<div class="modal-title mb-1 mt-1">
				<p>Вы действительно хотите {{title}} элемент?</p>
			</div>

			<div class="modal-control btn-wrap ">
				<button class="btn btn-success" @click="successHandler">Да</button>
				<button class="btn btn-danger" @click="closeModal">Нет</button>
			</div>
		</div>
	</div>
</template>

<script>
	import {EventBus} from "../bus/eventBus";
	import {modalConfig} from "../plugins/modal";
	const clearEditItem = {
		id: null,
		text: null,
		checked: null
	}
	export default {
		name: "Modal",
		data: () => ({
			direction: null,
			title: null,
			visible: false,
			editItem: clearEditItem,
			deleteItemById: null
		}),
		methods: {
			toggleVisible() {
				this.visible = !this.visible
			},
			successHandler() {
				if (this.direction === 'delete') {
					this.$store.dispatch('deleteItem', this.deleteItemById)
					this.$router.push('/')
				} else {
					this.$store.dispatch('editItem', this.editItem)
					this.$router.push('/')
				}
				this.closeModal()
			},
			clearData() {
				this.editItem = clearEditItem
				this.deleteItemById = null
				this.title = null
				this.direction = null
			},
			closeModal() {
				this.toggleVisible()
				this.clearData()
			},
		},
		created() {
			EventBus.$on(modalConfig.edit, (params) => {
				this.toggleVisible()
				this.title = 'изменить'
				this.direction = 'edit'
				this.editItem = params
			})
			EventBus.$on(modalConfig.delete, (params) => {
				this.toggleVisible()
				this.title = 'удалить'
				this.direction = 'delete'
				this.deleteItemById = params
			})
		},
		beforeDestroy() {
			EventBus.$off(modalConfig.edit)
			EventBus.$off(modalConfig.delete)
		}
	}
</script>

<style lang="scss">
	.modal-container {
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0, 0, 0, .45);
		justify-content: center;
		align-items: flex-start;
		padding-top: 3rem;
		z-index: 1000;

		&:not(.active) {
			display: none;
		}

		&.active {
			display: flex;
		}

		& .modal {
			min-width: calc(320px - 1rem);
			max-height: 50vh;
			background-color: #fff;
			border-radius: 2px;
			padding: 1rem;
		}
	}
</style>