import Vue from 'vue'

import { EventBus } from '../bus/eventBus'

export const modal = {
	install(Vue) {
		Vue.prototype.$modal = function (eventName, eventParams) {
			EventBus.modal(eventName, eventParams)
		}
	},
}

Vue.use(modal)

export const modalConfig = {
	delete: 'delete-item',
	edit: 'edit-item',
}
