<template>
	<div>
		<h1 class="mb-1">{{current === 'edit' ? 'Редактировать' : 'Добавить'}}</h1>
		<div>
			<div class="d-flex justify-content-between align-items-center mb-1">
				<div class="btn-wrap">
					<button
							@click="sendForm"
							:class="{'btn-current' : current === 'edit'}"
							class="btn btn-success material-icons"
							:disabled="form.text.length < valueLength"
							title="Сохранить"
					>save</button>
					<button
						v-if="current === 'edit'"
						class="btn btn-danger"
						@click="deleteItem"
						title="Удалить"
					><i class="material-icons">delete</i>
					</button>
					<button
						v-if="current === 'add'"
						class="btn btn-primary"
						@click="clear"
						:disabled="form.text.length === 0"
						title="Очистить"
					><i class="material-icons">backspace</i></button>
				</div>
				<p v-if="form.text.length < valueLength" class="color-danger ml-1 mb-1">Минимальная длина {{valueLength}} символов</p>
			</div>
			<div class="checkbox-wrap mb-1" v-if="current === 'edit'">
				<input
						type="checkbox"
						v-model="form.checked"
						:id="form.id"
						:checked="form.checked"/>
				<label :for="form.id">
						<span
								:class="form.checked ? 'color-success': 'color-danger'"
						>Задача {{form.checked ? 'выполнена' : 'не выполнена'}}</span>
				</label>
			</div>
			<textarea
					:class="{'area-danger': form.text.length < valueLength}"
					class="textarea"
					v-model="form.text"/>
		</div>
	</div>
</template>

<script>
	import {v4 as uuidv4} from 'uuid';
	import {mapGetters} from "vuex";
	import {modalConfig} from "../plugins/modal";
	// TODO: Поправить проверку
	export default {
		name: "EditPage",
		data: () => ({
			valueLength: 6,
			current: '',
			form: {
				text: '',
				checked: false,
				id: null
			}
		}),
		computed: {
			...mapGetters(['items'])
		},
		created() {
			const id = this.$route.query.id
			if (id !== undefined) {
				this.current = 'edit'
				this.form = {...this.items.find(item => item.id === id)}
			} else {
				this.current = 'add'
			}
		},
		methods: {
			clear() {
				this.form.text = ''
			},
			deleteItem() {
				this.$modal(modalConfig.delete, this.form.id)
			},
			sendForm() {
				const {text, checked} = this.form
				const data = {
					checked,
					text,
					id: this.form.id ? this.form.id : uuidv4()
				}
				if (this.current === 'edit') {
					this.$modal(modalConfig.edit, data)
				} else {
					this.$store.dispatch('addItem', data)
					this.form.text = ''
				}
			}
		}
	}
</script>

<style lang="scss">
	.btn-current {
		margin-left: auto;
	}
</style>