import Vue from 'vue'
import Vuex from 'vuex'
import { LocalStorage } from '../plugins/localStorage'

Vue.use(Vuex)

export const store = new Vuex.Store({
	state: {
		currentIndex: 0,
		history: [],
	},
	getters: {
		items: (state) => state.history[state.currentIndex],
	},
	mutations: {
		addHistory(state, payload) {
			state.history = [...payload]
			state.currentIndex += 1
		},
		editHistory(state, payload) {
			state.history = [...state.history, [...payload]]
			state.currentIndex = state.history.length - 1
		},
		historyBack(state) {
			if (state.currentIndex >= 0) {
				state.currentIndex -= 1
			}
		},
		forward(state) {
			if (state.history.length - 1 !== state.currentIndex) {
				state.currentIndex += 1
			}
		},
	},
	actions: {
		addHistory({ commit, state }, payload) {
			commit('editHistory', payload)
		},
		addItem({ commit, state }, payload) {
			const newHistoryItem = [
				...state.history[state.currentIndex],
				payload,
			]
			commit('editHistory', newHistoryItem)
		},
		editItem({ commit, state }, payload) {
			const editHistoryItem = state.history[state.currentIndex].filter(
				(item) => item.id !== payload.id
			)
			commit('editHistory', [...editHistoryItem, payload])
		},
		deleteItem({ commit, state }, payload) {
			const deleteHistoryItem = state.history[state.currentIndex].filter(
				(item) => item.id !== payload
			)
			commit('editHistory', deleteHistoryItem)
		},
	},
})

store.subscribe((mutation, { history, currentIndex }) => {
	LocalStorage.writeTodoItems(history[currentIndex])
})
