<template>
	<div id="app" class="container">
		<div class="d-flex justify-content-between navigation-wrap">
			<Navigation />
			<div class="btn-wrap">
				<button
						@click="cancel"
						class="btn btn-primary"
						:disabled="currentIndex === 0"
				>
					<span class="material-icons" title="Назад">keyboard_arrow_left</span>
				</button>
				<button
						@click="forward"
						class="btn btn-primary"
						:disabled="currentIndex === history.length - 1"
				>
					<span class="material-icons" title="Вперед">keyboard_arrow_right</span>
				</button>
			</div>
		</div>
		<router-view/>
		<Modal />
	</div>
</template>

<script>
	import Modal from "./components/Modal";
	import db from './plugins/db.json'
	import {LocalStorage} from "./plugins/localStorage"
	import Navigation from "./components/Navigation";
	import {mapState} from "vuex";
	export default {
		components: {Navigation, Modal},
		methods: {
			cancel() {
				this.$store.commit('historyBack')
			},
			forward() {
				this.$store.commit('forward')
			}
		},
		computed: {
			...mapState([
				'currentIndex',
				'history',
			])
		},
		created() {
			const ls = LocalStorage.findTodoItems()
			this.$store.dispatch('addHistory', ls ? ls : db)
		}
	}
</script>

<style lang="scss">
	@import "./styles/main";

	.navigation-wrap {
		margin-top: 1rem;
		margin-bottom: 2rem;
	}
</style>
